let typesOfRepresent = {
  // 开通级别
  ruleType: [{
    value: 1,
    label: '店主'
  },{
    value: 2,
    label: '服务商'
  },{
    value: 3,
    label: '运营商'
  }],
  // 用户类型
  userType: [{
    value: 1,
    label: '内部员工'
  },{
    value: 2,
    label: '外部合作'
  },{
    value: 3,
    label: '其他'
  }],
  // 白名单审核状态
  whiteAuthStatus: [{
    value: 0,
    label: '全部'
  },{
    value: 1,
    label: '未审核'
  },{
    value: 2,
    label: '审核通过'
  },{
    value: 3,
    label: '已拒绝'
  },{
    value: 4,
    label: '已取消'
  }],
  // 用户身份类型
  identifyType: [{
    value: -1,
    label: '全部用户'
  },{
    value: 0,
    label: '普通用户'
  },{
    value: 1,
    label: '店主'
  },{
    value: 2,
    label: '服务商'
  },{
    value: 3,
    label: '运营商'
  }],

  getLabelByValue: (list, value)=> {
    let label = '';
    for (let i= 0; i< list.length; i++) {
      if (list[i].value === value) {
        label = list[i].label;
        break;
      }
    }
    return label;
  }
}
module.exports = typesOfRepresent
// export default typesOfRepresent;


