
const validate = {
  checkLoginUser: (rule, value, callback) => {
    if (!value) {
      callback(new Error('账号不能为空'));
    }
    callback();
  },
  checkLoginPass: (rule, value, callback) => {
    if (!value) {
      callback(new Error('密码不能为空'));
    }
    callback();
  },
  checkInviterId: (rule, value, callback) => {
    if (!value) {
      callback(new Error('请输入ID'));
    } else if (!/^[1-9]\d*$/.test(value)) {
      callback(new Error('ID为纯数字'));
    } else {
      callback();
    }
  },
};

export default validate;



